import React, { useEffect, useState } from 'react';
import { Grid, Typography, Box, 
         Link, Card, CardContent, 
         CardActionArea, CardMedia } from '@mui/material';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CircularProgress from '@mui/material/CircularProgress';
import { API } from '../Tools/API';

const darkTheme = createTheme({
  palette: {
    mode: 'dark',
    primary: {
      main: '#1976d2',
    },
  },
});

export default function Portfolio() {
  const [myportfolio, setMyPortfolio] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      await fetch(`https://sheets.googleapis.com/v4/spreadsheets/${API.SPREADSHEET}/values/${API.SPREADSHEET_PORTFOLIO}?key=${API.KEY}`)
      .then(res => res.json())
      .then(data => {
        setMyPortfolio(data.values.slice(1).reverse());
      });
    };

    fetchData();

  }, []);

  return(
    <ThemeProvider theme={darkTheme}>
      <Grid columns={{ xs: 12, sm: 12, md: 12 }} sx={{ mt: 15 }}>
        {myportfolio.length > 0 ? 
          <Box
            sx={{ 
              pl: 5, pr: 5
            }}
          >
            <Grid container>
              {myportfolio.length > 0 ?
                myportfolio.map((portfolio, i) => (
                  <Grid 
                    item 
                    xs={12} 
                    key={i} 
                    sx={{ 
                      borderBottom: '1px solid #121212', 
                      '&:last-child': {
                        borderBottom: 'none'
                      }
                    }}
                  >
                    <Card sx={{ boxShadow: 'none', borderRadius: 0, backgroundColor: 'transparent', backgroundImage: 'none' }}>
                      <Link href={portfolio[2]} target="_blank" sx={{ color: 'white' }} underline="none">
                        <CardActionArea sx={{ display: 'flex' }}>
                          <CardMedia
                            component="img"
                            image={portfolio[5]}
                            alt={portfolio[3]}
                            sx={{ width: 151 }}
                          />
                          <CardContent sx={{ textAlign: 'justify' }}>
                            <Typography gutterBottom variant="h5" component="div">
                              {portfolio[3]}
                            </Typography>
                            <Typography variant="subtitle2" sx={{ fontSize: 16 }}>
                              {portfolio[1]}
                            </Typography>
                            <Typography variant="subtitle1" color="text.secondary" sx={{ fontSize: 18 }}>
                              {portfolio[4]}
                            </Typography>
                          </CardContent>
                        </CardActionArea>
                      </Link>
                    </Card>
                  </Grid>
                ))
                :
                <Typography variant="h6" component="div">
                  No Data
                </Typography>
              }
            </Grid>
          </Box>
          :
          <div style={{ display: 'flex' }}>
            <CircularProgress sx={{ margin: 'auto' }} />
          </div>
        }
      </Grid>
    </ThemeProvider>
  )
}