import React, { useEffect, useState } from 'react';
import { Grid, Typography, Box, 
         IconButton, Link, Avatar, 
         Card, CardContent, CardActionArea } from '@mui/material';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { API } from '../Tools/API';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';
import EmailIcon from '@mui/icons-material/Email';
import CircularProgress from '@mui/material/CircularProgress';

const darkTheme = createTheme({
  palette: {
    mode: 'dark',
    primary: {
      main: '#1976d2',
    },
  },
});


export default function Home() {
  const [widthBody, setwidthBody] = useState(window.innerWidth);
  const [mydata, setMyData] = useState([]);
  const [skills, setSkills] = useState([]);
  const [myportfolio, setMyPortfolio] = useState([]);
  const sosmedLabel = ['LinkedIn','GitHub','Email'];
  const sosmedIcon = [<LinkedInIcon sx={{ fontSize: 30 }} />,<GitHubIcon sx={{ fontSize: 30 }} />,<EmailIcon sx={{ fontSize: 30 }} />];

  useEffect(() => {
    const fetchData = async () => {
      await Promise.all([
        fetch(`https://sheets.googleapis.com/v4/spreadsheets/${API.SPREADSHEET}/values/${API.SPREADSHEET_DATA}?key=${API.KEY}`).then(res1 => res1.json()),
        fetch(`https://sheets.googleapis.com/v4/spreadsheets/${API.SPREADSHEET}/values/${API.SPREADSHEET_SKILLS}?key=${API.KEY}`).then(res2 => res2.json()),
        fetch(`https://sheets.googleapis.com/v4/spreadsheets/${API.SPREADSHEET}/values/${API.SPREADSHEET_PORTFOLIO}?key=${API.KEY}`).then(res3 => res3.json())
      ]).then(([data1,data2,data3]) => {
        setMyData(data1.values.slice(1));
        setSkills(data2.values.slice(1));
        setMyPortfolio(data3.values.slice(1).reverse());
      });
    };

    const handleResize = () => {
      setwidthBody(window.innerWidth)
    }

    window.addEventListener('resize', handleResize)

    fetchData();

  }, [window.innerWidth]);

  return(
    <ThemeProvider theme={darkTheme}>
      <Grid columns={{ xs: 12, sm: 12, md: 12 }} sx={{ mt: 15 }}>
        <Grid container spacing={{ xs: 2, md: 3 }} columns={{ xs: 4, sm: 8, md: 12 }} sx={{ pt: '130px', pb: '130px' }}>
          <Grid item xs={4} sm={8} md={6} style={widthBody > 900 ? { display: 'flex' } : { display: 'flex', marginBottom: 50 }}>
            <Box sx={{ margin: 'auto' }}>
              {mydata.length > 0 ? 
                <>
                  <Typography
                    variant="h5"
                    component="div"
                    align={widthBody > 900 ? 'left' : 'center'}
                  >
                    Hello, I am
                  </Typography>
                  <Typography
                    variant="h3"
                    component="div"
                    align={widthBody > 900 ? 'left' : 'center'}
                  >
                    {mydata[0][1]}
                  </Typography>
                  <Typography
                    variant="h6"
                    component="div"
                    align={widthBody > 900 ? 'left' : 'center'}
                  >
                    {mydata[0][2]}
                  </Typography>
                  <Box sx={{ mt: 2, display: 'flex' }}>
                    {mydata[0].slice(5).map((sosmed,i) =>
                      <Link 
                        key={i} 
                        href={sosmed} 
                        target="_blank"
                        sx={widthBody > 900 ?
                          { 
                            color: "white",
                            ml: 1, 
                            "&:first-child": {
                              ml: 0
                            }
                          }
                          :
                          {
                            color: "white",
                            "&:first-child": {
                              ml: 'auto'
                            },
                            "&:last-child": {
                              mr: 'auto'
                            }
                          }
                        }
                      >
                        <IconButton 
                          color="inherit" 
                          aria-label={sosmedLabel[i]} 
                          component="span" 
                        >
                          {sosmedIcon[i]}
                        </IconButton>
                      </Link>
                    )}
                  </Box>
                </>
                :
                <div style={{ display: 'flex' }}>
                  <CircularProgress sx={{ margin: 'auto' }} />
                </div>
              }
            </Box>
          </Grid>
          <Grid item xs={4} sm={8} md={6}>
            {mydata.length > 0 ? 
              <Avatar
                alt={mydata[0][1]}
                src={mydata[0][0]}
                sx={{ width: 300, height: 300, m: 'auto' }}
              />
              :
              <div style={{ display: 'flex' }}>
                <CircularProgress sx={{ margin: 'auto' }} />
              </div>
            }
          </Grid>
        </Grid>
        <Box 
          sx={{ 
            backgroundColor: '#121212',
            backgroundImage: 'linear-gradient(rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.05))', 
            p: '50px 50px'
          }}
        >
          {mydata.length > 0 ? 
          <>
            <Typography 
              variant="h4"
              component="div"
              sx={{
                mb: 2
              }}
            >
              Personal Detail
            </Typography>
            <Typography 
              variant="paragraph"
              component="div"
              align="justify"
              sx={{
                fontSize: 20,
                mb: 2
              }}
            >
              {mydata[0][3]}
            </Typography>
          </>
          :
            <div style={{ display: 'flex' }}>
              <CircularProgress sx={{ margin: 'auto' }} />
            </div>
          }
        </Box>
        <Box
          sx={{ pl: 5, pr: 5, pt: 10, pb: 10 }}
        >
          <Typography
            variant="h4"
            component="div"
            sx={{
              mb: 5
            }}
            align="center"
          >
            Skills
          </Typography>
          <Grid container spacing={{ xs: 4, md: 6 }} columns={{ xs: 4, sm: 8, md: 12 }}>
            {skills.length > 0 ?
              skills.map((skill, i) => (
                <Grid item xs={4} sm={8} md={6} key={i}>
                  <Card sx={{ minHeight: '270px', p: 2 }}>
                    <CardContent sx={{ textAlign: 'justify' }}>
                      <Typography gutterBottom variant="h5" component="div">
                        {skill[1]}
                      </Typography>
                      <Typography variant="body2" color="text.secondary" sx={{ fontSize: 18 }}>
                        {skill[2]}
                      </Typography>
                    </CardContent>
                  </Card>
                </Grid>
              ))
              :
              null
            }
          </Grid>
        </Box>
        <Box
          sx={{ 
            backgroundColor: '#121212',
            backgroundImage: 'linear-gradient(rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.05))', 
            pl: 5, pr: 5, pt: 10, pb: 10 
          }}
        >
          <Typography
            variant="h4"
            component="div"
            sx={{
              mb: 5
            }}
          >
            My Latest Projects
          </Typography>
          <Grid container>
            {myportfolio.length > 0 ?
              myportfolio.slice(0,5).map((portfolio, i) => (
                <Grid 
                  item 
                  xs={12} 
                  key={i} 
                  sx={{ 
                    borderBottom: '1px solid #121212', 
                    '&:last-child': {
                      borderBottom: 'none'
                    }
                  }}
                >
                  <Card sx={{ boxShadow: 'none', borderRadius: 0, backgroundColor: 'transparent', backgroundImage: 'none' }}>
                    <Link href={portfolio[2]} target="_blank" sx={{ color: 'white' }} underline="none">
                      <CardActionArea>
                        <CardContent sx={{ textAlign: 'justify' }}>
                          <Typography gutterBottom variant="h5" component="div">
                            {portfolio[3]}
                          </Typography>
                          <Typography variant="subtitle2" sx={{ fontSize: 16 }}>
                            {portfolio[1]}
                          </Typography>
                          <Typography variant="subtitle1" color="text.secondary" sx={{ fontSize: 18 }}>
                            {portfolio[4]}
                          </Typography>
                        </CardContent>
                      </CardActionArea>
                    </Link>
                  </Card>
                </Grid>
              ))
              :
              <Typography variant="h6" component="div">
                No Data
              </Typography>
            }
          </Grid>
        </Box>
      </Grid>
    </ThemeProvider>
  )
}