import React, { useEffect, useState } from 'react';
import { Grid, Typography, Box, 
         IconButton, Link, Avatar } from '@mui/material';
import Timeline from '@mui/lab/Timeline';
import TimelineItem from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineOppositeContent from '@mui/lab/TimelineOppositeContent';
import TimelineDot from '@mui/lab/TimelineDot';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { API } from '../Tools/API';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';
import EmailIcon from '@mui/icons-material/Email';
import CircularProgress from '@mui/material/CircularProgress';

const darkTheme = createTheme({
  palette: {
    mode: 'dark',
    primary: {
      main: '#1976d2',
    },
  },
});

export default function About() {
  const [widthBody, setwidthBody] = useState(window.innerWidth);
  const [mydata, setMyData] = useState([]);
  const [experiences, setExperiences] = useState([]);
  const sosmedLabel = ['LinkedIn','GitHub','Email'];
  const sosmedIcon = [<LinkedInIcon sx={{ fontSize: 30 }} />,<GitHubIcon sx={{ fontSize: 30 }} />,<EmailIcon sx={{ fontSize: 30 }} />];

  useEffect(() => {
    const fetchData = async () => {
      await Promise.all([
        fetch(`https://sheets.googleapis.com/v4/spreadsheets/${API.SPREADSHEET}/values/${API.SPREADSHEET_DATA}?key=${API.KEY}`).then(res1 => res1.json()),
        fetch(`https://sheets.googleapis.com/v4/spreadsheets/${API.SPREADSHEET}/values/${API.SPREADSHEET_EXPERIENCE}?key=${API.KEY}`).then(res2 => res2.json())
      ]).then(([data1,data2]) => {
        setMyData(data1.values.slice(1));
        setExperiences(data2.values.slice(1));
      });
    };

    const handleResize = () => {
      setwidthBody(window.innerWidth)
    }

    window.addEventListener('resize', handleResize)

    fetchData();

  }, [window.innerWidth]);

  return(
    <ThemeProvider theme={darkTheme}>
      <Grid columns={{ xs: 12, sm: 12, md: 12 }} sx={{ mt: 15 }}>
        <Grid container spacing={{ xs: 2, md: 3 }} columns={{ xs: 4, sm: 8, md: 12 }} sx={{ pt: '140px', pb: '140px' }}>
          <Grid item xs={4} sm={8} md={5}>
            {mydata.length > 0 ? 
              <Avatar
                alt={mydata[0][1]}
                src={mydata[0][0]}
                sx={{ width: 280, height: 280, m: 'auto' }}
              />
              :
              <div style={{ display: 'flex' }}>
                <CircularProgress sx={{ margin: 'auto' }} />
              </div>
            }
          </Grid>
          <Grid item xs={4} sm={8} md={7} style={widthBody > 900 ? { display: 'flex', paddingRight: 50 } : { display: 'flex', marginTop: 50, paddingLeft: 50, paddingRight: 50 }}>
            <Box sx={widthBody > 900 ? { margin: 'none'} : { margin: 'auto' }}>
              {mydata.length > 0 ? 
                <>
                  <Typography
                    variant="h3"
                    component="div"
                    align={widthBody > 900 ? 'left' : 'center'}
                  >
                    {mydata[0][1]}
                  </Typography>
                  <Typography
                    variant="h6"
                    component="div"
                    align={widthBody > 900 ? 'justify' : 'center'}
                  >
                    {mydata[0][4]}
                  </Typography>
                  <Box sx={{ mt: 2, display: 'flex' }}>
                    {mydata[0].slice(5).map((sosmed,i) =>
                      <Link 
                        key={i} 
                        href={sosmed} 
                        target="_blank"
                        sx={widthBody > 900 ?
                          { 
                            color: "white",
                            ml: 1, 
                            "&:first-child": {
                              ml: 0
                            }
                          }
                          :
                          {
                            color: "white",
                            "&:first-child": {
                              ml: 'auto'
                            },
                            "&:last-child": {
                              mr: 'auto'
                            }
                          }
                        }
                      >
                        <IconButton 
                          color="inherit" 
                          aria-label={sosmedLabel[i]} 
                          component="span" 
                        >
                          {sosmedIcon[i]}
                        </IconButton>
                      </Link>
                    )}
                  </Box>
                </>
                :
                <div style={{ display: 'flex' }}>
                  <CircularProgress sx={{ margin: 'auto' }} />
                </div>
              }
            </Box>
          </Grid>
        </Grid>
      </Grid>
      <Box>
        <Typography
          variant="h4"
          component="div"
          sx={{
            mb: 5
          }}
          align="center"
        >
          My Experiences
        </Typography>
        <Timeline position="alternate">
        {experiences.length > 0 ? 
          experiences.map((experience,i) => 
            <TimelineItem>
              <TimelineOppositeContent
                sx={{ m: 'auto 0' }}
                variant="body2"
                align={i % 2 === 0 ? "right" : "left"}
                color="text.secondary"
              >
                {experience[3]}
              </TimelineOppositeContent>
              <TimelineSeparator>
                <TimelineDot />
                <TimelineConnector />
              </TimelineSeparator>
              <TimelineContent>
                <Typography variant="h6" component="span">
                  {experience[1]}
                </Typography>
                <Typography>{experience[2]}</Typography>
              </TimelineContent>
            </TimelineItem>
            )
          :
            null
        }
        </Timeline>
      </Box>
    </ThemeProvider>
  )
}